import Page from '../../common/page';
import Toast from '../../wxcomponents/vant/toast/toast';

export default {
	data() {
		return {
			areaList: {},
			loading: true,
			value: 330302
		}
	},
	onLoad() {},
	methods: {
		onShow() {
			wx.request({
				url: 'https://cashier.youzan.com/wsctrade/uic/address/getAllRegion.json',
				success: response => {
					this.loading = false
					this.areaList = response.data.data
				}
			});
		},

		onChange(event) {
			const {
				values
			} = event.detail;

			Toast(values.map(item => item.name).join('-'));
		},

		onConfirm(event) {
			console.log(event);
		},

		onCancel(event) {
			console.log(event);
		}

	}
}
